* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Barlow', sans-serif;
}

.buttons-container {
  height: 10vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.buttons-container button {
  background: #fff;
  border: none;
  width: 8rem;
  height: 36px;
  margin: 10px;
  cursor: pointer;
  border-radius: 3px;
  font-weight: bolder;
  font-size: 16px;
  text-transform: uppercase;
  box-shadow: 3px 6px 9px rgba(0,0,0,0.05);
}

.buttons-container button:hover {
  opacity: 0.9;
  box-shadow: 3px 6px 9px rgba(0,0,0,0.1);
}

.modal {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.6);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  /* transition: 0.3s ease; */
}

.modal .modal-container {
  max-width: 1100px;
  width: 100%;
  height: 100%;
  background: #fff;
  border-radius: 5px;
  box-shadow: 5px 10px 15px rgba(0,0,0,0.2);
  /* transition: 0.5s ease 0.3s; */
  overflow: hidden;
}

.modal .modal-container .modal-header {
  height: 15%;
  border-bottom: 2px solid #aaaaaa;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.modal .modal-container .modal-header h2 {
  font-size: 2rem;
  padding-left: 3%;
  color: #333;
}

.modal .modal-container .modal-header button {
  width: 2.2rem;
  height: 2.2rem;
  margin-right: 15px;
  color: #333;
  cursor: pointer;
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;

  border: transparent;
  border-radius: 50%;
  background: transparent;

  -webkit-animation: closeBtnBlink 2s ease infinite;

          animation: closeBtnBlink 2s ease infinite;
  color: rgb(26, 161, 161);
}

.modal .modal-container .modal-header button:hover{
    transition: 0.2s ease;
    color: #dc3545;
    -webkit-animation: none;
            animation: none;
}

@-webkit-keyframes closeBtnBlink {
    0% {
        opacity: 1;
        font-weight: bold;
    }
    50% {
        opacity: 0.3;
    }
    100% {
        opacity: 1;
        font-weight: bold;
    } 
}

@keyframes closeBtnBlink {
    0% {
        opacity: 1;
        font-weight: bold;
    }
    50% {
        opacity: 0.3;
    }
    100% {
        opacity: 1;
        font-weight: bold;
    } 
}


.modal .modal-container .modal-content {
  height: 85%;
  overflow: auto;
}

.modal .modal-container .modal-content p {
  font-size: 17px;
  line-height: 1.5;
  color: #444;
  margin: 0px 0px 10px;
}


.hide {
  opacity: 0;
  visibility: hidden;
}

.modal .modal-container.hide {
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform: translateX(-10%);
          transform: translateX(-10%);
}
